import {Article} from './article';
import {Injectable} from '@angular/core';
import {Http, URLSearchParams} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {ActivatedRoute} from '@angular/router';

import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import {environment} from 'environments/environment';

@Injectable()
export class ArticleService {
  private _articles: BehaviorSubject<Article[]> = new BehaviorSubject<Article[]>([]);
  public articles: Observable<Article[]> = this._articles.asObservable();

  private _sources: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public sources: Observable<any> = this._sources.asObservable();

  private _refreshSubject: BehaviorSubject<string> = new BehaviorSubject<string>('');

  constructor(private http: Http) {
    this._refreshSubject.subscribe(this.getArticles.bind(this));
  }

  public getArticles(source = 'bbc-news'): void {
    this._makeHttpRequest('/v2/everything', source, 100)
      .map(json => json.articles)
      .subscribe(articleJSON => {
        const articles = articleJSON.map(article => Article.fromJSON(article));
        this._articles.next(articles);
      });
  }

  public refreshArticles(sourceId): void {
    this._refreshSubject.next(sourceId);
  }

  public getSources(): void {
    this._makeHttpRequest('/v2/sources')
      .map(json => json.sources)
      .filter(a => a.length > 0)
      .subscribe(this._sources);
  }

  private _makeHttpRequest(path: string,
                           sourceKey?: string, limit: any = 50): Observable<any> {
    const params = new URLSearchParams();
    params.set('apiKey', environment.apiKey);
    params.set('language', 'en');
    if (sourceKey && sourceKey !== '') {
      params.set('sources', sourceKey);
    }
    if (limit) {
      params.set('pageSize', limit);
    }

    return this.http
      .get(`${environment.baseUrl}${path}`, {search: params})
      .map(resp => resp.json());
  }
}



